.pager {
    margin: 20px;
    display: flex;
    justify-content: center;
    font-size: 14px;

    span {
        margin: 0 7px;
        cursor: pointer;

        color: blue;

        &.disabled {
            color: #ccc;
            cursor: not-allowed;
        }

        &.active {
            color: red;
            cursor: auto;
        }
    }

}